@import "./helpers/Colors.scss";

.icon-scroll,
.icon-scroll:before {
  position: absolute;
  margin: auto;
}

.title-scroll {
  position: absolute;
  color: $main-yellow;
  font-size: 0.8rem;
  font-weight: 600;
  top: 45px;
  left: -6px;
}

.icon-scroll {
  width: 20px;
  height: 40px;
  bottom: 25px;
  box-shadow: inset 0 0 0 1px rgba($main-yellow, 0.5);
  border-radius: 20px;
  opacity: 0.85;
  visibility: visible;
  transition: all ease-in-out 0.5s;

  @media only screen and (max-height: 600px) {
    visibility: hidden;
  }
}

.hide-icon-scroll {
  opacity: 0;
  visibility: hidden;
  transform: translateY(75px);
}

.icon-scroll:before {
  content: "";
  width: 6px;
  height: 6px;
  background: $main-yellow;
  margin-left: 7px;
  top: 25px;
  border-radius: 4px;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: scroll;
}

@keyframes scroll {
  0% {
    opacity: 0.75;
  }

  70% {
    opacity: 0;
  }
  71%,
  100% {
    opacity: 0;
    transform: translateY(-15px);
  }
}
